:root {
  --index: 1vh + 1vw;
  --h2h3: calc(var(--index) * 0.8);
}

.form {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  /* background: linear-gradient(0deg, #00a4e1, #015370); */
  background: #00a4e1;
}
.form__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 50vh;
}
.form__container h1 {
  text-align: center;
  font-size: calc(var(--index) * 1.5);
  margin: 0;
}
.form__container h2 {
  font-size: var(--h2h3);
  margin: 15px 0;
}
.form__container h3 {
  margin: 0;
  margin-top: 15px;
  font-size: var(--h2h3);
}
.form__container button {
  border: none;
  background-color: #015370;
  color: white;
  font-size: calc(var(--index) * 1);
  padding: calc(var(--index) * 0.05) calc(var(--index) * 0.5);
  border-radius: 10em;
  width: max-content;
  cursor: pointer;
  box-shadow: 2px 2px 5px black;
}
.form__container input {
  /* border: 1px solid #015370; */
  border: 1px solid rgba(255, 255, 255, 0.6);
  background-color: transparent;
  display: inline-block;
  font-size: calc(var(--index) * 0.5);
  padding: calc(var(--index) * 0.05) calc(var(--index) * 0.5);
  border-radius: 10em;
  color: white;
  min-width: calc(var(--index) * 24);
}
.form__container input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}
.form__company,
.form__other {
  display: flex;
  flex-direction: column;
  gap: calc(var(--index) * 0.3);
}
.form__fio div,
.form__contacts div {
  display: flex;
  gap: calc(var(--index) * 0.3);
}
.form__submit_and_private_police {
  margin-top: calc(var(--index) * 1);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form__submit_and_private_police input {
  min-width: max-content;
  width: calc(var(--index) * 0.7);
  height: calc(var(--index) * 0.7);
  margin-right: 10px;
  cursor: pointer;
}
.form__submit_and_private_police div:nth-child(2) {
  display: flex;
  align-items: center;
}
.form__submit_and_private_police div:nth-child(2) div {
  display: flex;
  align-items: center;
}
.form__submit_and_private_police div:nth-child(2) div span:nth-child(2) {
  font-size: calc(var(--index) * 0.5);
}

.form__private_police {
  margin-left: calc(var(--index) * 3);
  cursor: pointer;
}
.form__success {
  text-align: center;
  font-size: calc(var(--index) * 2);
}
@media (max-width: 720px) {
  .form__submit_and_private_police {
    flex-direction: column;
  }
  .form__submit_and_private_police div:nth-child(2) {
    margin-top: calc(var(--index) * 3);
  }
}
@media (max-width: 510px) {
  .form__fio div {
    display: flex;
    flex-direction: column;
  }
  .form__submit_and_private_police div:nth-child(2) {
    flex-direction: column;
    gap: 2.5vh;
  }
}
